export const supportedFileTypes = ['eventReport', 'visualization', 'map', 'eventVisualization'];
export const endpointFromFileType = fileType => {
  switch (fileType) {
    case 'visualization':
    case 'map':
    default:
      return "".concat(fileType, "s");
  }
};
export const appPathFor = (fileType, id) => {
  switch (fileType) {
    case 'visualization':
      return "dhis-web-data-visualizer/#/".concat(id);

    case 'map':
      return "dhis-web-maps/index.html?id=".concat(id);

    default:
      return;
  }
};